    separate(Number) {
        Number+= '';
        Number= Number.replace(',', '');
    
        var x = Number.split('.');
        var y = x[0];
        var z= x.length > 1 ? '.' + x[1] : '';
        var rgx = /(\d+)(\d{3})/;

        while (rgx.test(y))
            y= y.replace(rgx, `$1,$2`);
        return y+ z;
    }


    refreshList() {
        
        axios
        .get("/employeeslist")
        .then(res => this.setState({employeesList: res.data}, ()=> {
            var marriedCount = 0;
            var singleCount  = 0;
            var salary1000   = 0;
            var maxSalary = -1;
            var minSalary = 9999999999;
            var totalSalary = 0;
            
            for (var i = 0; i < this.state.employeesList.length; i++) {

                if (this.state.employeesList[i]["is_married"] === true) {
                    marriedCount++;
                } else {
                    singleCount++;
                }
    
                if (parseInt(this.state.employeesList[i]["salary"]) > 1000000) {
                    salary1000++;
                }
    
                if (parseInt(this.state.employeesList[i]["salary"]) > maxSalary) {
                    maxSalary = parseInt(this.state.employeesList[i]["salary"]);
                }
    
                if (parseInt(this.state.employeesList[i]["salary"]) < minSalary) {
                    minSalary = parseInt(this.state.employeesList[i]["salary"]);
                }
                totalSalary += parseInt(this.state.employeesList[i]["salary"]);
            }
    
            var mean = totalSalary / this.state.employeesList.length;
            var temp = 0;
    
            for (i = 0; i < this.state.employeesList.length; i++) {
                temp += Math.pow(parseInt(this.state.employeesList[i]["salary"]) - mean, 2);
            }
            
            this.setState({
                countMarried: marriedCount,
                totalSalary: this.separate(totalSalary),
                countSingle: singleCount,
                countSalary1000: salary1000,
                maximumSalary: this.separate(maxSalary),
                minimumSalary: this.separate(minSalary),
                salaryStandardDeviation: this.separate(parseInt(Math.sqrt(temp))),
    
            });
            
    
        
        }))
        .catch(err => alert(err));


    }



    componentDidMount() {

        this.refreshList();

        setInterval(
            () => this.setState({done: true}),
            900,
        );

    }

    updateInfo() {

    }
